var wx = require("weixin-js-sdk");
import { request } from "../assets/api/api.js"
import { Msg, Toast, Tabbar, TabbarItem, Flexbox, FlexboxItem } from "vux"
export default {
    components: {
        Msg,
        Toast,
        Tabbar,
        TabbarItem,
        Flexbox,
        FlexboxItem
    },
    data() {
        return {
            selectedTab: 1,
            userInfo: JSON.parse(sessionStorage.getItem("userInfo"))
        }
    },
    methods: {
        // 
        initConfig() {
            this.tooltip("success", "", 3000, "text", false);
            let companyId = this.userInfo.student.companyId;
            request.getTicketBySHA1(companyId).then(res => {
                if (res.success) {
                    this.tooltip("success", "", 3000, "text", false);
                    wx.config({
                        debug: false,
                        appId: res.object.appId,
                        timestamp: res.object.timestamp,
                        nonceStr: res.object.noncestr,
                        signature: res.object.sign,
                        jsApiList: ["scanQRCode"] // JSJS2
                    });
                    wx.error((res) => {
                        this.tooltip("error", "", 3000, "text", false);
                    });
                }
                else {
                    this.tooltip("error", "", 3000, "text", false);
                }
            });
        },
        // 
        scan() {
            wx.scanQRCode({
                desc: "scanQRCode desc",
                needResult: 1, // 01
                scanType: ["qrCode", "barCode"], // 
                // 
                success: (res) => {
                    if (res.errMsg === "scanQRCode:ok") {
                        let studentId = this.userInfo.student.userId;
                        let orderId = res.resultStr.split("&")[0].split("=")[1];
                        request.qr_sign([studentId, orderId]).then(res => {
                            if (res.success) {
                                this.tooltip("success", "", 3000, "text", () => {
                                    this.$router.push({ path: "/hasTrained", query: { id: res.object, type: "" } });
                                });
                            }
                            else {
                                this.tooltip("error", "", 3000, "text", false);
                            }
                        });
                    }
                },
                // 
                error: (res) => {
                    if (res.errMsg.indexOf("function_not_exist") > 0) {
                        this.tooltip("error", "", 3000, "text", false);
                    }
                }
            });
        },
        // 
        tabbarChanged(val) {
            this.selectedTab = val;
            if (val === 0) {
                this.$router.push({ path: "/" });
            }
            else if (val === 2) {
                this.$router.push({ path: "/mine" });
            }
        }
    },
    created() {
        this.initConfig();
    },
    mounted() {
        // this.tooltip("success", "", 2000, "text", () => {
        //     console.log("tips");
        // });
        // this.tooltip("error", "", 5000, "text", false);
    }
}



