import Vue from 'vue'
import Router from 'vue-router'
// 
import UserBind from '@/components/userBind'
// 
import Complete from '@/components/complete'
// -
import Reservation from '@/components/reservationBeforeExam'
// 
import ChoseCoach from '@/components/choseCoach'
// -
import ChoseCoachBill from '@/components/choseCoachBill'
// 
import PaySuccess from '@/components/paySuccess'
// 
import Mine from '@/components/mine'
// 
import HasTrained from '@/components/hasTrained'
// 
import Scan from '@/components/scan'


Vue.use(Router)

export default new Router({
  routes: [
    { path: '/', name: 'reservation', component: Reservation, meta: { title: '-', requireAuth: true }, hidden: true },
    // { path: '/reservation',name: 'reservation',component: Reservation,meta:{title:'-',requireAuth:true},hidden: true},
    { path: '/userBind', name: 'userBind', component: UserBind, meta: { title: '', requireAuth: true }, hidden: true },
    { path: '/complete', name: 'complete', component: Complete, meta: { title: '', requireAuth: true }, hidden: true },
    { path: '/choseCoach', name: 'choseCoach', component: ChoseCoach, meta: { title: '', requireAuth: true }, hidden: true },
    { path: '/choseCoachBill', name: 'choseCoachBill', component: ChoseCoachBill, meta: { title: '', requireAuth: true }, hidden: true },
    { path: '/paySuccess', name: 'paySuccess', component: PaySuccess, meta: { title: '', requireAuth: true }, hidden: true },
    { path: '/mine', name: 'mine', component: Mine, meta: { title: '', requireAuth: true }, hidden: true },
    { path: '/hasTrained', name: 'hasTrained', component: HasTrained, meta: { title: '', requireAuth: true }, hidden: true },
    { path: '/scan', name: 'scan', component: Scan, meta: { title: '', requireAuth: true }, hidden: true },
  ]
})



