import {
    XButton,
    Tab,
    Scroller,
    TabItem,
    Flexbox,
    Group,
    Radio,
    FlexboxItem
} from "vux"
import {
    global
} from "../assets/javascript/global.js"
import {
    request
} from "../assets/api/api.js"

export default {
    components: {
        XButton,
        Tab,
        Group,
        Scroller,
        Radio,
        Flexbox,
        FlexboxItem,
        TabItem
    },
    data() {
        return {
            days: [],
            isChooses: 0,
            isNoData: false,
            coaches: [],
            isDown: false,
            sortList: ["", "", "", ""],
            carType: this.$route.query.carType,
            course: this.$route.query.course,
            gmtBegin: new Date().Format('yyyy-MM-dd'),
            gmtEnd: new Date().Format('yyyy-MM-dd'),
            sorts: '',
            userInfo: JSON.parse(sessionStorage.getItem('userInfo')),
            pullupConfig: {
                content: '',
                pullUpHeight: 60,
                height: 40,
                autoRefresh: false,
                downContent: '',
                upContent: '',
                loadingContent: '...'
            },
            status: {
                pullupStatus: 'default'
            },
            page:1,
            totalPage:1,
            deadlineMsg: ''
        }
    },
    computed: {
        height: function() {
            // let app = navigator.userAgent;
            // if(app.indexOf('iPhone')>-1){
            //     return 570
            // }else if(app.indexOf('Android')>-1){
            //     return 450
            // }
            let height_ = document.documentElement.clientHeight;
            return (height_ - 120);
        },
        isAll: function() {
            return this.page > this.totalPage
        }
    },
    methods: {
        // 
        onPullUpLoad(){
            //  page    
            if (this.page < this.totalPage) {
                this.$refs.scrollerBottom.enablePullup();
                this.page += 1;
                this.queryLists();
                this.$nextTick(() => {
                    this.$refs.scrollerBottom.donePullup();
                });
            } else {
                //  page    
                this.page += 1;
                this.$refs.scrollerBottom.disablePullup();
                this.$nextTick(() => {
                    this.$refs.scrollerBottom.reset();
                    //   
                    this.deadlineMsg = '';
                });
            }
        },
        // 
        sortChange(val) {
            // console.log(val);
            this.isDown = false;
            switch (val) {
                case '':

                    break;
                case '':

                    break;
                case '':

                    break;
                default:
                    break;
            }
        },
        // 
        getDay() {
            let timestamp = Date.now();
            let tmpDays = [];
            // 
            for (let i = 0; i < 5; i++) {
                tmpDays.push(new Date(timestamp + 8.64e7 * i));
            }
            // 
            tmpDays.forEach(day => {
                this.days.push(day.Format('yyyy-MM-dd'));
            });
        },
        // 
        onItemClick(index) {
            this.isChooses = index;
            this.isDown = false;
            this.gmtBegin = this.days[index];
            this.gmtEnd = this.days[index];
            this.queryLists();
        },
        //  id 
        goRouter(item) {
            // console.log(item);
            this.$router.push({
                path: "/choseCoachBill",
                query: {
                    id: item.schedule.id,
                    carType: this.$route.query.carType,
                    course: this.$route.query.course
                }
            });
        },
        // 
        queryLists() {
            let params = [this.userInfo.student.companyId, this.carType, this.course, this.gmtBegin, this.gmtEnd];
            let tmpArr = [];
            this.coaches = [];
            request.query(params).then(res => {
                // console.log(res);
                if (res.success) {
                    this.totalPage = res.object.totalPage;
                    if (res.object.list.length) {
                        res.object.list.forEach((item, index) => {
                            if (!!item.car.coach && item.schedules.length) {
                                tmpArr.push(item);
                            } else {
                                this.isNoData = true;
                            }
                        });
                        if (tmpArr.length) {
                            tmpArr.forEach(item => {
                                let car = item.car;
                                if (!!item.schedules[0].tempCoach) {
                                    car.coach = item.schedules[0].tempCoach;
                                }
                                this.coaches.push({
                                    car: car,
                                    id: item.id,
                                    schedule: item.schedules[0]
                                });
                            });
                        }
                    } else {
                        this.isNoData = true;

                    }
                } else {
                    this.tooltip("error", '', 5000, "text", false);
                }
            });
        }
    },
    created() {
        this.getDay();
        this.queryLists();
        // console.log('documentElement.clientHeight: ' + document.documentElement.clientHeight);
    }
}



