import {
    Flexbox,
    FlexboxItem,
    Group,
    GroupTitle,
    Cell,
    Calendar,
    XButton,
    InlineXNumber,
    XDialog,
    Tabbar,
    Scroller,
    TabbarItem
} from "vux"
import {
    global
} from "../assets/javascript/global.js"
import {
    request
} from "../assets/api/api.js"

export default {
    components: {
        Flexbox,
        Group,
        GroupTitle,
        Cell,
        XButton,
        Scroller,
        Calendar,
        InlineXNumber,
        FlexboxItem,
        XDialog,
        Tabbar,
        TabbarItem
    },
    data() {
        return {
            lists: [],
            selectedTab: 2,
            userInfo: JSON.parse(sessionStorage.getItem('userInfo')),
            date: '',
            page: 1,
            totalPage: 1,
            deadlineMsg: '',
            pageSize: global.pageSize,
            pullupConfig: {
                content: '',
                pullUpHeight: 60,
                height: 40,
                autoRefresh: false,
                downContent: '',
                upContent: '',
                loadingContent: '...'
            },
            status: {
                pullupStatus: 'default'
            },
            isNoData:false
        }
    },
    computed: {
        isAll: function() {
            return this.page > this.totalPage
        }
    },
    methods: {
        onScroll() {
            // console.log('onScroll')
        },
        onPullUpLoad() {
            //  page    
            if (this.page < this.totalPage) {
                this.$refs.scrollerBottom.enablePullup();
                this.page += 1;
                this.getBillHistory();
                this.$nextTick(() => {
                    this.$refs.scrollerBottom.donePullup();
                });
            } else {
                //  page    
                this.page += 1;
                this.$refs.scrollerBottom.disablePullup();
                this.$nextTick(() => {
                    this.$refs.scrollerBottom.reset();
                    //   
                    this.deadlineMsg = '';
                });
            }
        },
        // 
        cellClick(item) {
            var demo = this.format(item, 'font');
            this.$router.push({
                path: '/hasTrained',
                query: {
                    id: item.id,
                    type: demo
                }
            });
        },
        // 
        tabbarChanged(val) {
            this.selectedTab = val;
            if (val === 0) {
                this.$router.push({ path: "/" });
            }
            else if (val === 1) {
                this.$router.push({ path: "/scan" });
            }
        },
        // 
        format(temp, type) {
            if (temp.status === 'PAYED') {
                if (!temp.trainingRecord) {
                    return type === 'bg' ? '#04BE02' : ''
                } else {
                    if (temp.trainingRecord.status === 'doing') {
                        return type === 'bg' ? '#04BE02' : ''
                    } else if (temp.trainingRecord.status === 'finish') {
                        return type === 'bg' ? '#58B7FF' : ''
                    }
                }
            } else if (temp.status === 'UN_PAY') {
                return type === 'bg' ? 'rgba(255,73,73,0.84)' : ''
            } else if (temp.status === 'REFUND') {
                return type === 'bg' ? '#F7BA2A' : ''
            } else if (temp.status === 'ABANDON') {
                return type === 'bg' ? 'rgba(255,73,73,0.84)' : ''
            }
        },
        // 
        getBillHistory() {
            let args = [this.userInfo.student.companyId, this.userInfo.student.userId, this.page, this.pageSize, 'PAYED,REFUND,ABANDON'];
            request.queryOrder(args).then(res => {
                if (res.success) {
                    this.totalPage = res.object.totalPage;
                    if (res.object.list.length) {
                        res.object.list.forEach(item => {
                            this.lists.push({
                                carType: item.carType,
                                coach: item.coach,
                                companyId: item.companyId,
                                course: item.course,
                                date: item.date,
                                fee: item.fee,
                                gmtCreate: item.gmtCreate,
                                gmtPay: item.gmtPay,
                                gmtRefund: item.gmtRefund,
                                id: item.id,
                                license: item.license,
                                operator: item.operator,
                                payTool: item.payTool,
                                quantity: item.quantity,
                                scheduleId: item.scheduleId,
                                sequence: item.sequence,
                                status: item.status,
                                student: item.student,
                                tradeNo: item.tradeNo,
                                trainingRecord: item.trainingRecord,
                                unit: item.unit,
                                verifyCode: item.verifyCode,
                                way: item.way
                            });
                        });
                    }else{
                        this.isNoData = true;
                    }
                } else {
                    this.tooltip("error", '', 5000, "text", false);
                }
            });
        }
    },
    created() {
        this.getBillHistory();
    }
}



