import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

// 
const state = {
    showLoading: false
}

//  mutations
const mutations = {
    LOADING(state) {
        state.showLoading = true;
    },
    LOADED(state){
        state.showLoading = false;
    }
}
const getters = {
    showLoading:state => state.showLoading
}
//  store 
export default new Vuex.Store({
    state,
    mutations,
    getters
});


